//
// Box custom
// --------------------------------------------------

// Text block
//
.text-block {
  * {
    display: inline;
  }

  *:not(:last-child) {
    margin-right: .25em;
  }
}

* + .text-block {
  margin-top: 18px;
}

@media (min-width: $screen-sm-min) {
  * + .text-block {
    margin-top: 35px;
  }
}

// Box minimal
//
.box-minimal {
  text-align: center;

  &[class*='unit-'] {
    @media (min-width: $screen-xs-min) {
      text-align: left;
    }
  }

  .unit-left {
    .box-minimal-icon {
      padding-top: 4px;
    }
  }
}

.box-minimal-icon {
  font-size: 40px;
  line-height: 50px;
  color: $mine-shaft;
}

.box-minimal-title {
  @include header-base-styles;
  @include header-h4;
  font-weight: 700;
}

.box-minimal-title.big {
  font-size: 18px;
}

.box-minimal-divider {
  width: 36px;
  height: 4px;
  margin-left: auto;
  margin-right: auto;
  background: $brand-primary;
}

.box-minimal-text {
  width: 100%;
  max-width: 320px;
  margin-left: auto;
  margin-right: auto;
}

.box-minimal-modern {
  padding-top: 20px;
  padding-bottom: 20px;

  @media (min-width: $screen-sm-min) {
    padding-right: 15px;
    min-height: 183px;
  }

  @media (min-width: $screen-lg-min) {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  .box-minimal-icon {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 42px;
    will-change: transform;

    &:before,
    &:after {
      content: "";
      display: inline-block;
      height: 0;
      width: 3px;
      background-color: $brand-success;
      transition: 350ms ease;
    }
  }

  .box-minimal-title {
    a {
      color: $gray-darker;
      &:hover {
        color: $brand-success;
      }
    }
  }

  &:hover {
    .box-minimal-icon {
      &:before,
      &:after {
        height: 15px;
        margin-top: 8px;
        margin-bottom: 8px;
      }
    }
  }
}

h5.box-minimal-title {
  font-size: 18px;
  font-weight: 900;

  & + .box-minimal-text {
    margin-top: 9px;
  }
}

* + .box-minimal-title {
  margin-top: 10px;
}

* + .box-minimal-divider {
  margin-top: 10px;
}

* + .box-minimal-text {
  margin-top: 15px;
}

// Counter box
//
.counter-wrap {
  * + & {
    margin-top: 30px;
  }
}

.counter-box {
  padding: 15px;
  color: $gray-base;
  text-align: center;

  .counter {
    font: 900 45px $font-family-base;
    line-height: 1;
    text-transform: uppercase;
  }

  .counter-box-header {
    font: 400 12px/24px $font-family-base;
    text-transform: uppercase;
  }
  @media (min-width: $screen-sm-min) {
    padding: 0 15px;
  }
}

.counter-k {
  &:after {
    content: 'k';
  }
}

.counter-plus {
  &:after {
    content: '+';
  }
}

.counter-percent {
  &:after {
    content: '%';
    padding-left: 5px;
    font-family: inherit;
    font-weight: inherit;
    font-size: 24.25px;
    vertical-align: top;
  }
}

* + .counter-box-header {
  margin-top: 5px;
}

@media (min-width: $screen-lg-min) {
  .counter-box {
    .counter {
      font-size: 80px;
    }

    .counter-box-header {
      font-size: 14px;
    }
  }
}

// Counter variant 2
//
.counter-box-bold {
  @include display-flex;
  @include flex-direction(column);
  @include flex-wrap(nowrap);
  @include align-items(center);
  @include justify-content(center);
  padding: 40px 10px;
  text-align: center;
  .counter {
    @include header-base-styles;
    font-size: 78px;
    line-height: 1;
    text-transform: uppercase;
    letter-spacing: 0;

    @media (min-width: $screen-lg-min) {
      font-size: 80px;

    }
  }

  .counter-k:after {
    font-size: .5em;
  }

  .counter-box-header {
    font: 400 14px $font-family-sec;
    letter-spacing: 0;
    text-transform: uppercase;
  }
}

@media (min-width: $screen-lg-min) {
  .counter-box-bold {
    padding: 35px 5px 35px;
  }

  .counter-box-bold-2 {
    @media (max-width: $screen-lg-max) {
      .counter {
        font-size: 64px;
      }
    }

    .counter-box-header {
      font-size: 14px;
    }
  }
}

@media (min-width: $screen-xl-min) {
  .counter-box-bold {
    padding: 60px 15px 60px;

    &.counter-box-bold-2 {
      padding: 30px 10px 30px;
    }
  }
}

// Counter box minimal
//
.counter-box-minimal {
  text-align: center;
  .counter {
    @include header-base-styles;
    font-size: 34px;
    line-height: 1;
    letter-spacing: 0;
    vertical-align: middle;
    white-space: nowrap;

    @media (min-width: $screen-sm-min) {
      font-size: 44px;
    }

    @media (min-width: $screen-md-min) {
      font-size: 50px;
    }

    @media (min-width: $screen-lg-min) {
      font-size: $font-size-h1;
    }

    &:after {
      display: inline-block;
      padding: 0 .1em;
      font: inherit;
      font-weight: 400;
      font-size: .45em;
      vertical-align: middle;
    }
  }

  &-title {
    font-family: $font-family-sec;
  }
}

* + .counter-box-minimal-title {
  margin-top: 2px;
}

@media (min-width: $screen-md-min) {
  .counter-box-minimal-title {
    font-size: 18px;
  }
}

//counter box with border 
.counter-box-border {
  border: 1px solid $gray-1;
  padding: 38px 35px 33px;
  transition: .3s ease;

  display: flex;
  align-items: center;
  justify-content: center;

  .counter-icon {
    font-size: 66px;
    font-weight: 900;
    line-height: .5;
    color: $gray-4;

    &:before {
      padding-right: 7px;
      padding-top: 5px;
      font-size: 30px;
      color: $brand-success;
      vertical-align: top;
    }
  }

  .counter-box-header {
    margin-top: 20px;
    color: $gray;
  }

  &:hover {
    background: $brand-primary;
    border-color: $brand-primary;

    .counter-icon,
    .counter-icon:before,
    .counter-box-header {
      color: $white;
    }
  }
}

// counter boxed layout
.counter-boxed-layout {
  background-color: $gray-light;
  padding: 25px 15px;
  box-shadow: 3px 0 8px rgba($gray-base, .15);

  .counter,
  &.counter-box p.counter-box-header {
    color: $gray-darker;
  }

}

.counter-boxed-layout-mod-1 {
  background-color: $gray-light;
  padding: 25px 0 0;
  box-shadow: 3px 0 8px rgba($gray-base, .15);

  .counter {
    color: $gray-darker;
  }

  .counter-box-header {
    padding: 10px;
    background-color: $gray-darker;
    color: $white;
  }
}

// Box profile
//
.box-profile {
  text-align: left;
}

.box-profile-image {
  width: 100%;
  height: auto;
}

.box-profile-name {
  @include header-base-styles;
  @include header-h4;
}

.box-profile-description {
  font: 500 16px $font-family-sec;
  line-height: 1.2;
  letter-spacing: 0;
  color: $blue-marguerite;
}

.box-profile-text {
  * + p {
    margin-top: 14px;
  }

  * + .box-profile-title {
    margin-top: 20px;
  }
}

.box-profile-title {
  @include header-base-styles;
  @include header-h5;
}

* + .box-profile-body {
  margin-top: 30px;
}

* + .box-profile-description {
  margin-top: 0;
}

* + .box-profile-text {
  margin-top: 14px;
}

@media (min-width: $screen-md-min) {
  .box-profile {
    @include display-flex;
    @include flex-direction(row-reverse);
    @include flex-wrap(nowrap);
    @include align-items(stretch);
    @include justify-content(space-between);
  }

  .box-profile-left,
  .box-profile-body {
    width: calc(50% - 15px);
  }

  .box-profile-description {
    font-size: 18px;
  }

  * + .box-profile-text {
    margin-top: 19px;
  }

  .box-profile-text {
    * + p {
      margin-top: 19px;
    }

    * + .box-profile-title {
      margin-top: 30px;
    }
  }

  * + .box-profile-body {
    margin-top: 0;
  }
}

// Box inset
//
.box-inset {
  padding: 30px;
  background: $gray-light;
  text-align: left;
}

@media (min-width: $screen-sm-min) {
  .box-inset {
    padding: 60px 40px;
  }
}

// Box triangle
//
.box-triangle {
  background: $gray-light;
  text-align: left;
}

.box-triangle-image {
  width: 100%;
  height: auto;
}

.box-triangle-body {
  position: relative;
  padding: 25px 15px 30px 15px;
  background: $gray-light;

  &:before {
    content: '';
    position: absolute;
    left: 50%;
    top: -10px;
    @include transform(translateX(-50%) rotate(-45deg));
    width: 20px;
    height: 20px;
    background: $gray-light;
    z-index: 2;
    pointer-events: none;
  }

  > * {
    position: relative;
    z-index: 2;
  }
}

.box-triangle-body {
  * + .range {
    margin-top: 15px;
  }
}

.box-triangle + .box-triangle {
  margin-top: 30px;
}

@media (min-width: $screen-xs-min) {
  .box-triangle-body {
    padding: 30px 40px 40px 40px;
  }
}

@media (min-width: $screen-sm-min) {
  .box-triangle-sm-horizontal {
    @include display-flex;
    @include flex-direction(row);
    @include flex-wrap(nowrap);
    @include align-items(stretch);
    @include justify-content(space-between);
  }

  .box-triangle-sm-horizontal.box-triangle-reverse {
    @include flex-direction(row-reverse);
  }

  .box-triangle-sm-horizontal {
    .box-triangle-left {
      position: relative;
      overflow: hidden;
      @include flex-shrink(0);
      width: calc(50% - 15px);
    }

    .box-triangle-image {
      position: absolute;
      top: 50%;
      left: 50%;
      @include transform(translate(-50%, -50%));
      height: auto;
      width: auto;
      min-width: 101%;
      max-width: none;
      min-height: 101%;
    }

    .box-triangle-body {
      @include flex-grow(1);
      &:before {
        top: 50%;
        @include transform(translateY(-50%) rotate(-45deg));
        left: -10px;
      }
    }

    &.box-triangle-reverse {
      .box-triangle-body:before {
        left: auto;
        right: -10px;
      }
    }
  }

  .box-triangle-body {
    padding: 40px 40px 50px 30px;
  }
}

@media (min-width: $screen-lg-min) {
  .box-triangle {
    min-height: 389px;
  }

  .ie-10,
  .ie-11 {
    .box-triangle {
      &:after {
        content: '';
        display: inline-block;
        min-height: inherit;
      }
    }
  }
}

// Box cta
//
.box-cta {
  font-family: $font-family-sec;
  font-weight: 700;
  letter-spacing: 0;
  line-height: 1;
  text-transform: uppercase;
  color: $mischka;
  background: $gray-light;
  text-align: center;
}

.box-cta-body {
  padding: 30px 20px 20px;
  overflow: hidden;
  white-space: nowrap;
}

.box-cta-subtitle {

  > * + * {
    margin-top: 7px;
  }
}

.box-cta-title {
  font-size: 42px;
}

* + .box-cta-subtitle {
  margin-top: 10px;
}

.box-cta-subtitle-top {
  display: inline-block;
  font-size: 18px;
  padding: .4em .4em .2em;
  color: $white;
  background: $tan-hide;
}

.box-cta-subtitle-bottom {
  font-size: 36px;
}

.box-cta-control {
  min-width: 200px;

  .btn {
    padding: 18px;
  }
}

@media (min-width: $screen-xs-min) {
  .box-cta-title {
    font-size: 60px;
    padding-right: 10px;
  }

  .box-cta-subtitle-bottom {
    font-size: 44px;
  }

  * + .box-cta-subtitle {
    margin-top: 0;
  }
}

@media (min-width: $screen-sm-min) {
  .box-cta {
    @include display-flex;
    @include flex-direction(row);
    @include justify-content(center);
    text-align: left;
  }

  .box-cta-body {
    @include display-flex;
    @include flex-direction(row);
    @include flex-wrap(nowrap);
    @include align-items(stretch);
    @include justify-content(flex-start);
    padding: 17px 36px 10px;
    min-width: (100% - 28.2%);
  }

  .box-cta-control {
    @include justify-content(flex-start);

    &, > * {
      @include display-flex;
      @include flex-direction(column);
      @include flex-wrap(nowrap);
      @include align-items(center);
    }

    > * {
      @include justify-content(center);
      @include flex-grow(1);
      width: 100%;
      max-height: 130px;
    }
  }

  .box-cta-title {
    @include display-flex;
    @include flex-direction(column);
    @include align-items(flex-start);
    @include justify-content(center);
    font-size: 52px;
    padding-right: 25px;
  }

  .box-cta-subtitle-top {
    font-size: 12px;
  }

  .box-cta-subtitle-bottom {
    font-size: 18px;
  }
}

@media (min-width: $screen-md-min) {
  .box-cta-title {
    font-size: 66px;
  }

  .box-cta-control {
    width: 100%;
    min-width: 240px;
    max-width: 28.2%;
  }

  .box-cta-subtitle-top {
    font-size: 14px;
  }

  .box-cta-subtitle-bottom {
    font-size: 34px;
  }
}

@media (min-width: $screen-lg-min) {
  .box-cta-body {
    padding: 17px 36px 0;
  }

  .box-cta-title {
    font-size: 97px;
  }

  .box-cta-subtitle-top {
    font-size: 18px;
  }

  .box-cta-subtitle-bottom {
    font-size: 48px;
  }

  .box-cta-control {
    width: 100%;
    max-width: 28.2%;
  }
}

// Box icon
//
.box-icon {
  text-align: center;
}

.box-icon-image {
  max-width: 350px;
  margin-left: auto;
  margin-right: auto;

  img {
    width: 100%;
    height: auto;
    transition: $transition-md;
    opacity: 1;
    transform: scale(1);

    &:hover {
      opacity: .9;
      transform: scale(1.05);
    }
  }
}

.box-icon-body {
  a {
    &:hover {
      text-decoration: underline;
    }
  }
}

* + .box-icon-body {
  margin-top: 10px;
}

@media (min-width: $screen-xs-min) {
  .box-icon-body {
    padding: 0 5px;
  }
}

// Box line
//
.box-line {
  position: relative;
  background: $gray-light;
  padding: 30px 20px 40px;

  &:before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 4px;
    background: $brand-primary;
  }

  &__mod-1 {
    overflow: hidden;
    max-width: 350px;
    margin-left: auto;
    margin-right: auto;

    &:after {
      content: '';
      position: absolute;
      top: -20px;
      right: -30px;
      width: 90px;
      height: 50px;
      background-color: $white;
      transform: rotate(40deg);
    }
  }
}

.box-line-success {
  &:before {
    background: $brand-success;
  }
}

.box-line + * {
  margin-top: 35px;
}

.box-line + .box-line {
  margin-top: 30px;
}

@media (min-width: $screen-sm-min) {
  .box-line {
    padding: 40px;
  }

  .box-line + * {
    margin-top: 45px;
  }
}

// Box form
//
.box-form {
  position: relative;
  z-index: 10;
  box-shadow: 0 0 16px 0 rgba($gray-base, .11);
  background: $white;
  padding: 30px 20px 45px;

  .rd-mailform .select2-container--bootstrap .select2-selection {
    min-width: 100px;
  }

  .select2-container--bootstrap .select2-selection {
    background: $gray-light;
  }

  .btn {
    min-height: $input-height-base;
  }
}

.box-form-1 {
  margin-top: -30px;
}

@media (min-width: $screen-sm-min) {
  .box-form-2 {
    padding: 50px;
  }
}

@media (min-width: $screen-lg-min) {
  .box-form-1 {
    padding: 44px 100px 50px 74px;
  }
}

// Box text
.page .box-text {
  #{headings()} {
    color: $mischka;
  }

  > * + * {
    margin-top: 0;
  }

  .btn {
    min-width: 220px;
  }

  @media (min-width: $screen-lg-min) {
    h1,
    .h1 {
      font-size: 97px;
    }

    .h3,
    h3 {
      font-size: 36px;
    }
  }
}

// Box wrap
//
.box-wrap {
  position: relative;
}

.box-wrap-content {
  padding: 30px 0;
  pointer-events: none;
  border-bottom: 1px solid $hr-border;
  text-align: center;
}

.box-wrap-content-interactive {
  pointer-events: auto;
}

@mixin box-wrap-overlap {
  border-bottom: 0;

  .box-wrap-map {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    @include display-flex;
    background: $gray-light;

    .rd-google-map {
      @include display-flex;
      width: 100%;
      height: 100%;
    }
  }

  .box-wrap-content {
    position: relative;
    z-index: 2;
    padding: 72px 0;
    border: 0;
  }
}

@media (max-width: $screen-xs-max) {
  .box-wrap {
    .shell {
      max-width: 100%;
    }
  }
}

@media (min-width: $screen-xs-min) {
  .box-wrap {
    @include box-wrap-overlap;
  }

  .box-wrap-content {
    text-align: right;
  }

  .box-wrap-content-interactive {
    display: inline-block;
  }
}

// Box contacts
//
.box-contacts {
  display: inline-block;
  padding: 30px 40px;
  background: $white;
  text-align: left;

  address {
    max-width: 145px;
  }

  h4 {
    font-size: 13px;
    text-transform: uppercase;
    letter-spacing: 0.1em;
  }

  * + h4,
  * + .h4 {
    margin-top: 30px;
  }
  h4 + *,
  .h4 + * {
    margin-top: 7px;
  }
}

@media (min-width: $screen-sm-min) {
  .box-contacts {
    padding: 50px 60px;
  }
}

@media (min-width: $screen-md-min) {
  .box-contacts {
    padding: 50px 100px;
  }
}

// Box tabs
//
.box-tabs {
  padding: 50px 20px;
  text-align: center;
  background: $white;
  box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.11);
  @include display-flex;
  @include flex-direction(column);
  @include flex-wrap(nowrap);
  @include align-items(center);
  @include justify-content(center);

  .tabs-custom {
    max-width: 540px;
    margin-left: auto;
    margin-right: auto;
  }

  .tabs-custom .nav-tabs,
  .tabs-custom .tab-content {
    text-align: center;
  }
}

@media (min-width: $screen-sm-min) {
  .box-tabs {
    padding: 70px 50px;
  }
}

@media (min-width: $screen-xl-min) {
  .box-tabs {
    margin: -40px -30px;
  }
}

// Box spacer
//
.box-spacer {
  padding: 40px 20px;
  color: $gray-darker;

  .tabs-custom {
    max-width: 520px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: $screen-lg-min) {
  .box-spacer {
    padding: 100px 50px 50px;
  }
}

// Box spacer md
//
.box-spacer-md {
  @include display-flex;
  @include flex-direction(row);
  @include flex-wrap(wrap);
  @include align-items(center);
  @include justify-content(center);
  padding: 20px;
  background: $white;

  .box-spacer-title {
    @include header-base-styles;
    font-size: 56px;
    line-height: 1;
    color: $brand-success;
  }
}

@media (min-width: $screen-sm-min) {
  .box-spacer-md {
    padding: 50px 65px;

    .box-spacer-title {
      font-size: 56px;
    }
  }
}

@media (min-width: $screen-lg-min) {
  .box-spacer-md {

    .box-spacer-title {
      font-size: 95px;
    }
  }
}

// Box item
//
.box-item {
  max-width: 290px;
  padding: 10px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.box-item-icon {
  font-size: 40px;
  color: $brand-primary;
}

* + .box-item-title {
  margin-top: 14px;
}

* + .box-item-text {
  margin-top: 11px;
}

@media (min-width: $screen-sm-min) {
  .box-item {
    padding: 0 0 10px;
  }
}

// Box promo
//
@media (max-width: $screen-xs-max) {
  .box-promo {
    max-width: 300px;
