// Box bordered
//
.box-bordered {
  position: relative;
  margin-top: 38px;
  text-align: center;
  @include display-flex;
  @include flex-direction(column);
  @include flex-wrap(wrap);
  @include align-items(center);
  @include justify-content(center);

  &,
  &-icon {
    background: $white;
  }

  border: 5px solid $gray-light;
  border-top: 0;

  h3 {
    font-size: 30px;
    span + span {
      margin-left: .25em;
    }
  }

  &:before,
  &:after {
    content: '';
    position: absolute;
    top: 0;
    border-top: 5px solid $gray-light;
  }

  &:before {
    left: 0;
    right: calc(50% + 40px);
  }

  &:after {
    left: calc(50% + 40px);
    right: 0;
  }

  * + p {
    margin-top: 10px;
  }
}

.box-bordered-icon {
  position: absolute;
  top: -38px;
  left: 50%;
  z-index: 2;
  transform: translateX(-50%);
  width: 87px;
  height: 87px;
  border-radius: 100%;
  line-height: 80px;
  color: $brand-primary;
  font-size: 40px;
  border: 5px solid $gray-light;

  &:before {
    position: relative;
    display: block;
    transition: .6s all ease;
  }
}

.box-bordered-body {
  padding: 75px 20px 40px;
  max-width: 100%;
}

@media (min-width: $screen-sm-min) {
  .box-bordered-body {
    padding: 60px 35px 40px;
  }
}

.desktop {
  @media (min-width: $screen-md-min) {
    .box-bordered {
      position: relative;
      transform: scale(1);
      -webkit-filter: blur(0);
      transition: .44s all ease;
      cursor: default;

      &:before,
      &:after {
        transition: .3s all ease;
      }

      &:hover {
        transform: scale(1.03);

        //&:before {
        //  left: 0;
        //  right: calc(50% + 90px);
        //}
        //
        //&:after {
        //  left: calc(50% + 90px);
        //  right: 0;
        //}
      }
    }
  }
}

@media (min-width: $screen-lg-min) {
  .box-bordered {
    margin: 0 10px;
  }

  .box-bordered-body {
    padding: 80px 65px 50px;
  }
}

//box contact info
.box-contact-info {
  background-color: $wild-sand;
  width: 100%;
}

.box-contact-item {
  padding: 40px 0;
  .unit-body {
