}

.box-bordered-variant-2 {
  position: relative;
  padding: 25px 15px;
  background-color: $white;
  perspective: 1000px;
  overflow: hidden;

  .box-minimal-divider {
    margin-top: 17px;
    margin-bottom: 20px;
    transition: $transition-md;
  }

  .box-minimal-icon {
    transition: $transition-md;
  }

  &:before {
    position: absolute;
    top: 15px;
    bottom: 15px;
    left: 15px;
    right: 15px;
    content: "";
    display: inline-block;
    border: 1px solid $brand-success;
    transition: $transition-md;
    transform: translate3d(0, 0, 150px);
    pointer-events: none;
  }

  @media (min-width: $screen-sm-min) {
