      padding: 20px 0;
    }
  }

  @media (min-width: $screen-md-min) {
    .owl-stage {
      padding: 20px 10px;

      > .owl-item {
        &:first-of-type {
          > * {
            margin-right: -10px;
          }
        }
        &:last-of-type {
          > * {
            margin-left: -10px;
