//========================================
// animated box
//========================================

.animated-box-wrap {
  perspective: 1000px;
  position: relative;
  max-width: 700px;

  * + h4 {
    margin-top: 15px;
  }
  html:not([data-x-mode]) & {
    &:hover .animated-box {
      transform: rotateY(180deg);

      .animated-box-back .animated-box-inner {
        transform: translate3d(0, 0, 0);
      }
      .animated-box-front .animated-box-inner {
        transform: translate3d(0, 0, 90px);
      }
    }
    &-vertical {
      &:hover .animated-box {
        transform: rotateX(180deg);
      }
    }
  }
  
  &.active {
    .animated-box {
      transform: rotateY(180deg);

      .animated-box-back .animated-box-inner {
        transform: translate3d(0, 0, 0);
      }
      .animated-box-front .animated-box-inner {
        transform: translate3d(0, 0, 90px);
      }
    }
    
    &.animated-box-wrap-vertical {
      .animated-box {
        transform: rotateX(180deg);
      }
    }
  }
  

  &-vertical {
    .animated-box-back {
      transform: rotateX(-180deg);
    }
  }
}

.animated-box {
  transform-style: preserve-3d;
  border: 1px solid $gray-light;
  box-shadow: $shadow-area-xs;
  text-align: center;
  transition: all 0.7s cubic-bezier(0.5, 0.12, 0.46, 0.88);
}

.animated-box-inner {
  transition: all 0.5s cubic-bezier(0.5, 0.12, 0.46, 0.88);
}

.animated-box-front,
.animated-box-back {
  display: flex;
  justify-content: center;
  align-items: center;
  transform-style: preserve-3d;
  min-height: 250px;
  padding: 25px;
  transition: all 0.1s 0.25s cubic-bezier(0.5, 0.12, 0.46, 0.88);

  &.bg-image { 
    background-size: cover;
    @extend %context-dark;
    &:before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: rgba($gray-base, .3);
    }
  }

  @media (min-width: $screen-sm-min) {
    min-height: 350px;
  }
}

.animated-box-front {
  background-color: $white;
  backface-visibility: hidden;
  z-index: 1;
  transform: translate3d(0, 0, 1px);

  .animated-box-inner {
    transform: translate3d(0, 0, 0);
  }
}

.animated-box-back {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 0;

  transform: rotateY(180deg) translate3d(0, 0, 1px);
  background-color: $brand-primary;

  .animated-box-inner {
    transform: translate3d(0, 0, 110px);
  }

  .btn-white-outline {
    &,
    &:active,
    &:focus {
      color: $gray-darker;
      background: transparent;
      border-color: $gray-darker;
    }

    &:hover {
      color: $gray-darker;
      background: $secondary-4;
      border-color: $secondary-4;
    }
  }
}

.ie-10,
.ie-11,
.ie-edge {

  .animated-box-wrap {
    .animated-box-back {
      opacity: 0;
    }
    &:hover {
      .animated-box-front {
        opacity: 0;
        pointer-events: none;
      }
      .animated-box-back {
        opacity: 1;
      }
    }
  }

  .animated-box-front,
  .animated-box-back {
    height: 250px;

    @media (min-width: $screen-sm-min) {
      height: 350px;
    }
  }
}

// anim box skyscraper
.animated-box-skyscraper {

  .animated-box-front,
  .animated-box-back {
    min-height: 300px;
    justify-content: space-between;
    flex-flow: row wrap;

    @media (min-width: $screen-sm-min) {
      min-height: 400px;
    }
  }
  .animated-box-inner {
    flex-grow: 1;
    width: 100%;
  }

  &-left {
    .animated-box {
      text-align: left;
    }
    hr {
      margin-left: 0;
    }
    .animated-box-front,
    .animated-box-back {
      align-items: flex-start;
    }
  }
  
  &-right {
    .animated-box {
      text-align: right;
    }
    hr {
      margin-right: 0;
    }
    .animated-box-front,
    .animated-box-back {
      align-items: flex-end;
    }
  }
  
  &-center {
    .animated-box-front,
    .animated-box-back {
      justify-content: center;
    }
    .animated-box-back {
      transform: rotateX(-180deg);
    }
  }
  
  &.active {
    &.animated-box-skyscraper-right {
      .animated-box {
        transform: rotateY(-180deg);
      }
    }
    &.animated-box-skyscraper-center {
      .animated-box {
        transform: rotateX(180deg);
      }
    }
  }
}
html:not([data-x-mode]) {
  .animated-box-skyscraper {
    &-right {
      &:hover .animated-box {
        transform: rotateY(-180deg);
      }
    }

    &-center {
      &:hover .animated-box {
        transform: rotateX(180deg);
      }
    }
  }
}

.ie-10,
.ie-11,
.ie-edge {
  .animated-box-skyscraper {
    .animated-box-front,
    .animated-box-back {
      height: 300px;

      @media (min-width: $screen-sm-min) {
        height: 400px;
      }
    }
  }
}

// anim box transparent
.animated-box-transparent {
  
  .animated-box {
    border-radius: 15px;
    border-color: $brand-success;
  }
  
  .animated-box-back {
    opacity: 0;
  }

  html:not([data-x-mode]) & {
    &:hover {
      .animated-box-front {
        opacity: 0;
        pointer-events: none;
      }
      .animated-box-back {
        opacity: 1;
      }
    }
  }
  
  &.active {
    .animated-box-front {
      opacity: 0;
      pointer-events: none;
    }
    .animated-box-back {
      opacity: 1;
    }
  }
 

  .animated-box-front,
  .animated-box-back {
    background: transparent;
  }
}


//anim box minimal
.animated-box-ordered {
  counter-reset: div;
  
  .animated-box-title {
    &:before{
      display: block;
      content: counter(div, decimal-leading-zero) '';
      counter-increment: div;
      font-size: 36px;
    }
  }

  .animated-box {
    border-radius: 15px;
    border-color: $brand-success;
  }

  .animated-box-ordered-item {
    .animated-box-front,
    .animated-box-back {
      border-radius: 15px;
      min-height: 220px;

      @media (min-width: $screen-sm-min) {
        min-height: 260px;
      }
    }
  }
  
  * + .btn {
    margin-top: 30px;
  }
}

// anim box product

.animated-box-wrap-product {
  .animated-box-back {
    background-color: $white;
    align-items: stretch;
    
    > * {
      flex-grow: 1;
      width: 100%;
    }

    .animated-box-inner {
      > * {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
      }
    }
  }
}

.ie-10,
.ie-11,
.ie-edge {
  .animated-box-wrap-product {
    .animated-box-front,
    .animated-box-back {
      height: auto;
      
    }
  }
}
