//
// Responsive Offsets
// --------------------------------------------------
@mixin responsive-offset($offset) {
  transform: translate3d(0,-$offset,0);
  margin-bottom: -$offset;
  pointer-events: none;

  > * {
    margin-top: $offset;
    pointer-events: auto;

    &::before {
      content: '';
      display: table;
      width: 0;
    }
  }
}

@mixin responsive-offset-media($offset) {
  transform: translate3d(0, -$offset, 0);
  margin-bottom: -$offset;
  > * { margin-top: $offset; }
}

.spacing-negative-1 {
  @include responsive-offset(-1px);
}

.spacing-0 {
  @include responsive-offset(0);
}
.spacing-10 {
  @include responsive-offset(10px);
}

.spacing-20 {
  @include responsive-offset(20px);
}

.spacing-30 {
  @include responsive-offset(30px);
}

.spacing-40 {
  @include responsive-offset(40px);
}

.spacing-50 {
  @include responsive-offset(50px);
}

.spacing-55 {
  @include responsive-offset(55px);
}

.spacing-70 {
  @include responsive-offset(50px);
}

.spacing-90 {
  @include responsive-offset(50px); 
}

@media (min-width: $screen-sm-min) {
  .spacing-70 {
    @include responsive-offset-media(70px);
  }

  .spacing-90 {
    @include responsive-offset-media(90px);
  }
}

// Insets
// -------------------------
.inset-left-25 {
  padding-left: 25px;
}

@media (min-width: $screen-md-min) {

  .inset-sm-25 {
    padding: 0 25px;
  }
}

// Sections
// -------------------------

.section-xs {
  padding: 40px 0;
}

.section-md-top {
  padding-top: 50px;
}

.section-md-bottom {
  padding-bottom: 50px;
}

.section-sm,
.section-md,
.section-lg,
.section-xl {
  padding: 60px 0;
}

@media (max-width: $screen-xs-max) {
  section {
    &:first-child {
      &.section-sm,
      &.section-md,
      &.section-lg,
      &.section-xl {
        padding-top: 40px;
      }
    }
  }
}

@media (min-width: $screen-sm-min) {
  section {
    &:first-child {
      &.section-sm {
        padding-top: 100px;
      }
    }
  }

  .section-md {
    padding: 70px 0;
  }

  .section-lg {
    padding: 80px 0;
  }

  .section-xl {
    padding: 95px 0;
  }
}

