    padding-bottom: 95px;
  }

  .section-lg {
    padding: 100px 0;
  }

  .section-xl {
    padding: 130px 0;
  }
}

@media (min-width: $screen-lg-min) and (min-height: $screen-height-desktop) {

  .section-xl {
    padding: 150px 0;
  }
}

